export default function processMinuses (text, lang) {
  var r = function (a, b) { return new RegExp(a, b) }
  text = text.replace(/&nbsp;|\u00a0/gi, ' ')
  text = text.replace(/([^\s])(\u2014)([^\s])/gm, '$1 $2 $3')
  text = text.replace(/\u2012|\u2013|\u002d|\u02d7|\u2212/gm, '-')
  text = text.replace(/\s+(-)\s+/gm, ' $1 ')
  text = text.replace(/([\u00bb\u201c"],)(-)/gm, '$1 $2')
  if (lang === 'ru') { // lang === 'ru'
    text = text.replace(/(\s|^|<p>)(["]*)(-)([\s]|$)/g, '$1' + '$2$4')
    text = text.replace(/([A-Za-z-0-9]) /g, '$1 ')
    text = text.replace(/([.,!?] |\n|^|<p>) ([A-Za-z-0-9"])/g, '$1 $2')
    //  
    var mst = '(|||||||||||||||||||||||||)'
    text = text.replace(r('([^-]|^)' + mst + '[ ]{2}?(||)([^-]|$)', 'gi'), '$1' + '$2-$3' + '$4')
      .replace(r('([^-]|^)' + mst + '[ ]{2}?(||)([^-]|$)', 'gi'), '$1' + '$2-$3' + '$4')
      .replace(r('([^-]|^)(|)[ ]{2}?' + mst + '([^-]|$)', 'gi'), '$1' + '$2-$3' + '$4')
      .replace(r('([^-]|^)(|)[ ]{2}?' + mst + '([^-]|$)', 'gi'), '$1' + '$2-$3' + '$4')
      .replace(/([\s]|^)()[ ]{2}?()([\s]|$)/gi, '$1' + '$2-$3' + '$4')
      .replace(/([\s]|^)()[ ]{2}?()([\s]|$)/gi, '$1' + '$2-$3' + '$4')
      .replace(/([-]{2,}) (|)([\s,.?!]|$)/g, '$1-$2' + '$3')
      .replace(/([^-]|^)([]|)[ ]{2}?()([^-]|$)/gi, '$1' + '$2-$3' + '$4')
      .replace(/((?:[ ]{2}))?[ ]{2}([\d.]+)-([\d]+)/gi, '$1 $2$3')
    //    
    text = text.replace(/([IVXLCDM]{1,3})-([IVXLCDM]{1,3})[ ]{2}??\.?([\s.,?!;)])/g, '$1$2 .$3')
      .replace(/([\d]{1,4})-([\d]{1,4})[ ]{2}??\.([\s.,?!;)])/g, '$1$2 .$3')
      .replace(/([^\d]|^)([0-2][0-9]:[0-5][0-9])-([0-2][0-9]:[0-5][0-9])([^\d]|$)/g, '$1' + '$2$3' + '$4')
    var mo = '(?:[]|[])'
    var to = '(?:[]|)'
    var month = '(' + mo +
      '|' + mo +
      '|' + to +
      '|' + mo +
      '|(?:[]|)|[]' + mo +
      '' + to +
      '|' + mo +
      '|' + mo +
      '|' + mo +
      '|' + mo +
    ')'
    text = text.replace(r('([\\s]|^)([1-3]?[\\d])-([1-3]?[\\d])[ ]{2}?' + month + '([^-]|$)', 'gi'), '$1' + '$2$3 $4' + '$5')
      .replace(r('([^-]|^)' + month + '-' + month + '([^-]|&)', 'gi'), '$1' + '$2$3' + '$4')
  }
  text = text.replace(/(\d)--(\d)/g, '$1$2')
    .replace(/([^-]|\s|^)--([^-]|$|\n)/g, '$1$2')
    .replace(/([^-\d]|^)(\d+)-(\d+)([^-\d]|$)/g, '$1' + '$2\u{2013}$3' + '$4')
    .replace(/([^a-z][a-z]|[--+=*/])-(\d)/g, '$1$2')
  text = text.replace(/\s+(-)\s+/gm, '$1')
  return text
}



